import React from "react";
import { useSelector } from "react-redux";
const styleBookingPage = {
  backgroundImage:
    "linear-gradient(to top,#000,transparent 70%),url(https://www.maxpixel.net/static/photo/1x/Red-Curtain-Cinema-Background-Theater-Stripes-551797.jpg",
  backgroundSize: "cover",
  backgroundRepeat: "no-repeat",
};
export default function BookingHistory() {
  let ticketListStore = useSelector((state) => state.ticketSlice.listTicket);

  let renderListTicket = () => {
    if (ticketListStore.length !== 0) {
      return ticketListStore.map((ticket) => {
        return (
          <div className="flex flex-col space-y-4">
            <span className="text-green-500 text-xl">
              Tên phim: {ticket.tenPhim}
            </span>
            <span className="text-green-500">Tên rạp: {ticket.tenRap}</span>
            <span className="text-green-500">Địa chỉ: {ticket.diaChi}</span>
            <span className="text-green-500">
              Thời gian chiếu: {ticket.gioChieu}
            </span>
            <span className="text-green-500">
              Ngày chiếu: {ticket.ngayChieu}
            </span>
            <div className="flex space-x-8">
              {ticket.listChoosenChair.map((chair) => {
                return (
                  <div className="flex flex-col">
                    <span className="text-amber-400">
                      Số ghế: {chair.tenGhe}
                    </span>
                    <span className="text-amber-400">
                      Giá tiền: {chair.giaVe + " VNĐ"}{" "}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
        );
      });
    }
  };
  return (
    <div
      style={styleBookingPage}
      className="w-screen h-screen text-white font-bold space-y-8 overflow-auto p-32"
    >
      <div>
        <h1 className="text-4xl font-bold text-center text-white">
          Lịch sử đặt vé
        </h1>
      </div>
      <div className="grid grid-cols-3 gap-10">{renderListTicket()}</div>
    </div>
  );
}
