import React, { useState, useEffect } from "react";
import { Carousel } from "antd";
import ItemMovie from "./ItemMovie";
import { nanoid } from "nanoid";
import { moviesServ } from "services/moviesService";
import phanTrang from "services/panigatePages";
import { PrevArrow } from "components/CustomArrow/CustomPrevArrow";
import { NextArrow } from "components/CustomArrow/CustomNextArrow";
import "./index.scss";

export default function CarouselTwo() {
  let [listMovieCarouselTwo, setListMovie] = useState([]);
  const getListMovieCarouselTwo = async () => {
    try {
      let res = await moviesServ.getMovieList();
      let dataPhim = res.data.content;
      let listMovieWithPages = phanTrang(dataPhim, 8);
      setListMovie(listMovieWithPages);
    } catch (err) {}
  };

  useEffect(() => {
    getListMovieCarouselTwo();
  }, []);
  let renderMovieList = () => {
    return listMovieCarouselTwo.map((arrayMovie) => {
      return (
        <div key={nanoid()}>
          <div className="container flex justify-center p-10">
            <div className="df:w-full sm:w-2/3 grid df:grid-cols-1 sm:grid-cols-4 gap-8">
              {arrayMovie.map((item) => {
                return <ItemMovie dataMovie={item} key={nanoid()} />;
              })}
            </div>
          </div>
        </div>
      );
    });
  };
  return (
    <div className="py-10 df:pt-0" id="carousel_two">
      <Carousel
        arrows={true}
        prevArrow={<PrevArrow />}
        nextArrow={<NextArrow />}
      >
        {renderMovieList()}
      </Carousel>
    </div>
  );
}
