import React, { useState } from "react";
import "./index.scss";
import { Card } from "antd";
import { NavLink } from "react-router-dom";
import { useDispatch } from "react-redux";
import { openModalTrailer } from "redux/slice/modalSlice";
import { btnVideoTrailer } from "assets/btnVideoTrailer";
const { Meta } = Card;

function ItemMovie({ dataMovie }) {
  let [isShowing, setIsShowing] = useState(false);
  let [customStyle, setCustomStyle] = useState({
    background: `url(${dataMovie.hinhAnh}) left top / cover no-repeat`,
    borderRadius: 4,
  });
  let dispatch = useDispatch();
  const showBtnTrailer = () => {
    if (isShowing === false) {
      setIsShowing(true);
    } else {
      return;
    }

    setCustomStyle({
      background: `linear-gradient(to top,#000,transparent 100%),url(${dataMovie.hinhAnh}) left top / cover no-repeat`,
      borderRadius: 4,
    });
  };
  const hideBtnTrailer = () => {
    setIsShowing(false);
    setCustomStyle({
      background: `url(${dataMovie.hinhAnh}) left top / cover no-repeat`,
      borderRadius: 4,
    });
  };

  return (
    <Card
      style={{ cursor: "pointer" }}
      className="df:w-full df:flex df:rounded  sm:block  sm:rounded-lg"
      cover={
        <div
          className="df:h-48 df:mr-5 sm:mr-0 sm:h-72 flex justify-center items-center"
          style={customStyle}
        >
          {isShowing ? (
            <button
              className="btn_video_trailer"
              onClick={() => {
                dispatch(openModalTrailer(dataMovie.trailer.substr(-11)));
              }}
            >
              <img width={60} src={btnVideoTrailer} alt="video-popup-button" />
            </button>
          ) : (
            <button
              className="btn_video_trailer df:hidden sm:block opacity-60 hover:opacity-100 hidden"
              onClick={() => {
                dispatch(openModalTrailer(dataMovie.trailer.substring(32)));
              }}
            >
              <img src={btnVideoTrailer} alt="video-popup-button" />
            </button>
          )}
        </div>
      }
      onMouseOver={showBtnTrailer}
      onMouseLeave={hideBtnTrailer}
    >
      {isShowing ? (
        <div className="my-2 flex justify-center ">
          <NavLink
            className="w-full  bg-amber-500 p-3 rounded text-center  text-white font-bold uppercase hover:text-white hover:bg-amber-600"
            to={`detail/${dataMovie.maPhim}`}
          >
            Đặt vé
          </NavLink>
        </div>
      ) : (
        <Meta
          title={
            <p className="df:ml-3 text-blue-800 uppercase font-bold">
              {dataMovie.tenPhim.length < 20
                ? dataMovie.tenPhim
                : dataMovie.tenPhim.substring(0, 20) + "..."}
            </p>
          }
          description={
            <div className="df:ml-3">
              <div className="df:h-20">
                <span className="text-gray-500 ">
                  {dataMovie.moTa.substring(0, 50) + "..."}
                </span>
              </div>
              <div className="my-2 sm:hidden  flex justify-center ">
                <NavLink
                  className="df:w-full bg-amber-500 df:p-4 rounded text-center  text-white font-bold uppercase hover:text-white hover:bg-amber-600"
                  to={`detail/${dataMovie.maPhim}`}
                >
                  Đặt vé
                </NavLink>
              </div>
            </div>
          }
        />
      )}
    </Card>
  );
}

export default React.memo(ItemMovie);
