import React, { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import { moviesServ } from "services/moviesService";
import { Tabs } from "antd";
import { nanoid } from "nanoid";

export default function MovieSchedules() {
  let [dataRaw, setDataRaw] = useState([]);

  let param = useParams();
  let { id } = param;

  let fetchData = async () => {
    try {
      let res = await moviesServ.getMovieScheduleServ(id);

      setDataRaw(res.data.content);
    } catch (err) {}
  };

  useEffect(() => {
    fetchData();
  }, []);

  let renderHeThongRap = () => {
    if (dataRaw.length !== 0) {
      return dataRaw.heThongRapChieu.map((heThongRap) => {
        return (
          <Tabs.TabPane
            tab={
              <div className="p-2">
                <img
                  className="w-20 h-20"
                  src={heThongRap.logo}
                  alt={heThongRap.logo}
                />
              </div>
            }
            key={nanoid()}
          >
            {heThongRap.cumRapChieu.map((movie) => {
              return (
                <div
                  key={nanoid()}
                  className=" movie__container flex border-b-2"
                >
                  <div className="w-5/6 movie__detail p-3 space-y-4">
                    <div className="space-x-2">
                      <span className="text-2xl font-bold">
                        {movie.tenCumRap}
                      </span>
                    </div>
                    <div className="grid grid-cols-4 gap-4">
                      {movie.lichChieuPhim.map((lichChieu) => {
                        return (
                          <div key={nanoid()}>
                            <NavLink
                              to={`/purchase/${lichChieu.maLichChieu}`}
                              className="border-solid border  space-x-2 border-gray-500 rounded p-1"
                            >
                              <span className="text-red-500 font-semibold">
                                {lichChieu.ngayChieuGioChieu.substring(0, 10)}
                              </span>
                              <span>~</span>
                              <span className="text-amber-500 font-semibold">
                                {lichChieu.ngayChieuGioChieu.substring(12, 19)}
                              </span>
                            </NavLink>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              );
            })}
          </Tabs.TabPane>
        );
      });
    }
  };
  return (
    <section className="movietabs space-y-5 py-10">
      <div>
        <p className="text-center text-2xl font-medium">LỊCH CHIẾU PHIM</p>
      </div>
      <div className="container flex justify-center p-5">
        <Tabs
          className="w-4/6 border-4 rounded-lg"
          tabPosition="left"
          defaultActiveKey="1"
        >
          {renderHeThongRap()}
        </Tabs>
      </div>
    </section>
  );
}
